import React, { Component } from "react";
import FormStyle from './FormStyle'

class ContactTwo extends Component{
    render(){
        return(
            <div className="contact-form--1">
                <div className="container">
                    <div className="row row--35 align-items-start">
                        <div className="col-lg-6 order-2 order-lg-1">
                            <div className="section-title text-left mb--50">
                                <span className="subtitle">Javite nam se</span>
                                <h2 className="title">Kontakt</h2>
                                <div className="im_address">
                                    <span>Nazovite nas direktno na:</span>
                                    <a className="link im-hover" href="phone">+385 99 440 1987</a>
                                </div>
                                <div className="im_address mt--5">
                                    <span>Napisite nam mail na:</span>
                                    <a className="link im-hover" href="email">jurica@zenerg.hr</a>
                                </div>
                            </div>
                            <FormStyle />
                        </div>
                        <div className="col-lg-6 order-1 order-lg-2">
                            <div className="mb_md--30 mb_sm--30">
                                <img src="/assets/images/about/logo-kontakt.png" alt="trydo"/>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
export default ContactTwo;